import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { LotterService } from '../lotter.service';
@Component({
  selector: 'app-add-persion',
  templateUrl: './add-persion.component.html',
  styleUrls: ['./add-persion.component.css'],
})
export class AddPersionComponent implements OnInit {
  formGroup: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    public _lotterService: LotterService
  ) {
    this.createFormInit();
  }

  ngOnInit() {}

  add() {
    let inputData = this.formGroup.getRawValue();
    let addPerson = {
      name: inputData.yourName,
      lastNum: inputData.yourId,
    };
    this._lotterService.addPersonData(addPerson);
    this.formGroup.get('yourName').setValue('');
    this.formGroup.get('yourId').setValue('');
  }

  clearAll() {
    this._lotterService.clearPersonData();
    this.formGroup.get('yourName').setValue('');
    this.formGroup.get('yourId').setValue('');
  }

  get yourNameControl(): FormControl {
    return this.formGroup.get('yourName') as FormControl;
  }

  get yourIdControl(): FormControl {
    return this.formGroup.get('yourId') as FormControl;
  }
  createFormInit() {
    this.formGroup = this.formBuilder.group({
      yourName: ['', [Validators.required]],
      yourId: [
        '',
        [
          Validators.required,
          Validators.minLength(3),
          Validators.maxLength(10),
        ],
      ],
    });
  }
}
